.vproductShow-card-container {
    margin: 0;
    padding: 0;
    list-style: none;
    .vproductShow-card-cell {
        border-bottom: 1px solid lightgray;
        padding: 5vw;
        img {
            width: 100%;
        }
        h2{
            margin: 0;
        }
        .infs{
            margin: 5px;
            color: gray;
        }
        .price-and-pop {
            margin: 8px;
            .price {
                display: inline-block;
                padding: 3px 10px;
                border-radius: 50px;
                color: rgba(206, 166, 55, 0.9);
                background-color: rgba(255, 231, 166, 0.9);
            }
            .pop {
                margin-left: 10px;
                display: inline-block;
                width: 40px;
                height: 18px;
                font-size: 14px;
                line-height: 18px;
                letter-spacing: 1px;
                text-align: center;
                border-radius: 15px;
                color: #999;
                border: 1px solid #999;
            }
        }
        .button-copy {
            height: 40px;
            background-image: linear-gradient(
                to right,
                #ff9000 0,
                #ff5000 100%
            );
            border: none;
            border-radius: 60px;
            font-size: 18px;
            color: white;
            margin: 5px;
        }
        .button-share {
            height: 40px;
            background-image: linear-gradient(
                to right,
                #ff9000 0,
                #ff5000 100%
            );
            border: none;
            border-radius: 60px;
            font-size: 18px;
            color: white;
            margin: 5px;
        }
    }
}
